.home {
  background-color: rgb(51, 51, 51);
  background-image: url(../images/index.jpeg);
  background-size: cover;
  min-height: 100vh;
}
.home__container {
  min-height: 100vh;
  background: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0.733) 10%,
    rgba(0, 0, 0, 0.1) 20%,
    rgba(0, 0, 0, 0.1) 70%,
    rgba(0, 0, 0, 0.5) 90%
  );
  padding: 15% 8%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  color: rgb(241, 255, 241);
}
.home a {
  background: rgb(241, 255, 241);
  padding: 16px 40px;
  font-size: 1.1rem;
  text-decoration: none;
  letter-spacing: 1px;
  color: black;
}
.home a:hover {
  transform: translate3d(1px, 1px, 0);
}
.home h1 {
  text-transform: uppercase;
  font-size: 32px;
}
.home p {
  font-size: 20px;
  line-height: 130%;
  margin: 18px 0;
}
